export default {
	data() {
		return {
			border: '',
			round: '',
			size: '',
			shadow: '',
			bordersize: '',
			color: 'red',
			modalName: '',
			ColorList: [{
					title: '嫣红',
					name: 'red',
					color: '#e54d42'
				},
				{
