			]
		}
	},
	methods: {
		setRound(e) {
			let that = this
			that.round = e.detail.value
		},
		setSize(e) {
			let that = this
			that.size = e.detail.value
		},
		setShadow(e) {
			let that = this
			that.shadow = e.detail.value
		},
		setBorder(e) {
			let that = this
			that.border = e.detail.value
			if (!e.detail.value) {
				that.bordersize = false
			}
		},
		setBorderSize(e) {
			let that = this
			that.bordersize = e.detail.value
		},
		showModal(e) {
			let that = this
			that.modalName = e.currentTarget.dataset.target
		},
		hideModal() {
			let that = this
			that.modalName = ''
		},
		setColor(e) {
			let that = this
			that.color = e.currentTarget.dataset.color
			that.modalName = ''
		}
	}
}
